.logIn {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 25px 0;
}

.logIn .header{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    margin-bottom: 20px;
    margin-top: 20px;
}

.logIn .header h3{
    margin: 0;
}

.logIn form{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
}

.logIn form input{
    margin-bottom: 15px;
    padding: 10px 150px 10px 10px;
    background-color: #e7edf4;
    border-radius: 5px;
    border: none;
}

.logIn form button{
    margin-top: 20px;
    padding: 10px 150px;
    background-color: rgb(55, 55, 255);
    border-radius: 5px;
    border: none;
    color: #fff;
}

.logIn form div input{
    margin: 0;
    position: relative;
    top: 3px;
}
.logIn form div p{
    margin: 0 10px;
}

.logIn form div{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    text-align: center;
}