.Sign-up-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 0;
}

.Sign-up-header .header-links a {
  text-decoration: none;
  color: #000;
  background-color: #e7edf4;
  padding: 10px 20px;
  border-radius: 5px;
  transition: all 0.3s ease;
  text-align: center;
}

.Sign-up-header .header-links a:hover {
  background-color: #fff;
  color: #000000;
}

.Sign-up-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 50px;
}

.Sign-up-form h2 {
  margin-bottom: 20px;
}

.Sign-up-form form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.Sign-up-form form input {
  margin-bottom: 15px;
  padding: 10px 150px 10px 10px;
  background-color: #e7edf4;
  border-radius: 5px;
  border: none;
}

.Sign-up-form form button {
  margin-top: 20px;
  padding: 10px 150px;
  background-color: rgb(55, 55, 255);
  border-radius: 5px;
  border: none;
  color: #fff;
}

.Sign-up-form form p {
  margin-top: 10px;
  font-size: 12px;
  color: #666;
  text-align: center;
}
